<template>
  <div class="cardinfo">
    <table width="98%"   style="margin-right:2%;font-size:12px;">
      <tr>
        <td colspan="8" class="lab"><span><img src="../../image/titleicon02.png" width="26" height="26" alt=""/></span> 卡片信息</td>
      </tr>
      <tr>
        <td width="80" align="right">卡&emsp;&emsp;号：</td>
        <td><input type="text" v-model="info.CardID"  readonly/></td>
        <!--<td width="80" align="right">IC表厂别名：</td>-->
        <!--<td><input type="text" v-model="factoryCN"  readonly/></td>-->
        <td width="80" align="right">卡类型：</td>
        <td><input type="text" v-model="cardtype[index]"  readonly/></td>
      </tr>
      <tr>
        <td width="80" align="right">卡上{{cardtype[index]}}：</td>
        <td ><input type="text" v-model="info.Gas" style="color: red;font-weight: bold;" readonly/>{{unit}}</td>
        <td width="80" align="right">卡上{{cardtype[index+1]}}：</td>
        <td ><input type="text" v-model="info.Money" style="color: red;font-weight: bold;" readonly/>{{unit}}</td>
      </tr>
    </table>
  </div>
</template>
<script>

export default {
  title: '卡上信息',
  data () {
    return {
      cardInfo: {},
      cardtype: ['气量', '金额'],
      index: 0,
      number: 0,
      // factoryCN: '',
      units: ['方', '元']
    }
  },
  props: ['info'],
  ready () {
    // this.factoryCN = Util.FactoryTypeConver(this.info.Factory)
  }
}
</script>



